@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;400&family=Roboto:wght@100;300;400&display=swap);
* {
  margin: 0;
  font-family: "Josefin Sans", sans-serif;
}

.app {
  background-color: #fafafa;
  height: 100vh;
}

.app__headerImage,
.app__header_mobileview {
  padding: 10px;
  background-color: white;
  border-bottom: 1px solid lightgray;
  object-fit: contain;
  display: flex;
  justify-content: space-around;
}

.app__headerInput {
  width: 15rem;
  display: flex;
  align-items: flex-end;
  background: #f3f0f0;
  padding-left: 11px;
  border-radius: 5px;
}

.app__searchinput {
  padding: 10px;
  border: none;
  background: inherit;
  outline: none;
}

.app__headerLinks {
  width: 20rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.app__headerLinks a {
  text-decoration: none;
  color: black;
}

.app__posts {
  display: flex;
  justify-content: center;
  padding: 20px;
}

.app__posts_right__header {
  display: flex;
  align-items: center;
  padding: 20px;
}

.post__avatar {
  margin-right: 10px;
}

.app__footer_mobileview,
.app__header_mobileview {
  display: none;
}

/* Media queries */
@media screen and (max-width: 580px) {
  .app__headerImage,
  .app__headerLinks,
  .app__headerInput,
  .app__posts_right {
    display: none;
  }

  .app__posts {
    padding: unset;
  }

  .app__header_mobileview,
  .app__footer_mobileview {
    display: flex;
    justify-content: space-between;
  }

  .app__footer_mobileview {
    position: fixed;
    bottom: 0%;
    width: 95%;
    background-color: white;
    border: 1px solid lightgray;
    padding: 10px;
    align-items: center;
  }

  .app__footer_mobileview .post__avatar {
    width: 27px;
    height: 27px;
  }
}

.post {
  background-color: white;
  max-width: 500px;
  border: 1px solid lightgray;
  margin-bottom: 45px;
}
.post__image {
  width: 100%;
  object-fit: contain;
  border-top: 1px solid lightgray;
  border-bottom: 1px solid lightgray;
}

.post__text {
  padding: 5px 10px;
}

.post__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
}

.post__header_icon {
  display: flex;
  align-items: center;
}

.post__header_icon .MuiAvatar-root {
  width: 30px;
  height: 30px;
}

.post__avatar {
  margin-right: 10px;
}

.post__commentBox {
  display: flex;
  margin-top: 10px;
}

.post__input {
  flex: 1 1;
  border: none;
  padding: 10px;
  border-top: 1px solid lightgray;
}

.post__button {
  flex: 0 1;
  border: none;
  color: #6082a3;
  background-color: transparent;
  border-top: 1px solid lightgray;
}

.Mobile_view_threedots_icon {
  cursor: pointer;
}

.post__mobileview_div {
  display: none;
}

.post__text1,
.post__text2 {
  margin: 5px 10px;
}

.post__text2 {
  color: #767373;
  font-weight: 500;
}

.post__image_div {
  position: relative;
}

.post__image_hearIcon {
  text-align: center;
  animation: heartgrow 0.8s linear;
  -webkit-animation: heartgrow 0.8s linear;
}

@-webkit-keyframes heartgrow {
  100% {
    -webkit-transform: scale(2);
            transform: scale(2);
    opacity: 0;
  }
}

@keyframes heartgrow {
  100% {
    -webkit-transform: scale(2);
            transform: scale(2);
    opacity: 0;
  }
}

/* Media queries */
@media screen and (max-width: 580px) {
  .post__commentBox {
    display: none;
  }

  .post {
    max-width: none;
    border: none;
    margin-bottom: 15px;
  }

  .post__header {
    padding: 10px 10px;
  }

  .post__header_icon {
    font-size: 13px !important;
  }

  .post__mobileview_div {
    display: flex;
    justify-content: space-between;
  }
  .post__mobileview_div svg {
    margin: 10px 4px;
  }
}

.Profile__header {
  display: flex;
  justify-content: center;
  padding: 20px;
}

.Profile__left {
  margin: 0 50px;
}

.profile__right {
  width: 40%;
  margin-top: 20px;
}

.profile__right h1 {
  font-weight: 400;
}

.profile__text {
  width: 50%;
  margin: 20px 0;
  display: flex;
  justify-content: space-around;
}

hr {
  width: 60%;
  margin: 20px auto;
}

.profile__postsmain {
  width: 70%;
  display: flex;
  flex-wrap: wrap;
  margin: auto;
}

.profile__profilepost {
  width: 15rem;
  height: 18rem;
  margin: 10px;
}

.profile__postsmain_mobileview {
  display: none;
}

/* Media queries */
@media screen and (max-width: 580px) {
  .Profile__left .MuiAvatar-root {
    width: 75px;
    height: 75px;
  }

  .Profile__header {
    justify-content: flex-start;
  }

  .Profile__left {
    margin: unset;
    padding-top: 20px;
  }

  .profile__right {
    margin-left: 10px;
    width: 100%;
  }

  .profile__right h1 {
    font-size: 25px;
  }

  .profile__text {
    width: 100%;
    font-size: 15px;
  }

  .profile__postsmain {
    display: none;
  }

  .profile__postsmain_mobileview {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    margin-left: 10px;
  }

  .profile__profilepost_mobileview {
    width: 7rem;
    height: 8.6rem;
    margin: 4px;
    background-color: lightgreen;
  }
}

.createpost_parentDiv {
  height: 30rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.createpost_childDiv {
  width: 60%;
  border: 1px solid lightgray;
  padding: 20px;
  border-radius: 5px;
}

.createpost_childDiv h1 {
  font-weight: 400;
  margin: 30px 0;
}

.MuiFormControl-root {
  margin: 30px 0 !important;
  width: 34rem;
}

button:disabled,
button[disabled] {
  cursor: not-allowed !important;
  color: #fff !important;
  background: #1976d2 !important;
}

.css-sghohy-MuiButtonBase-root-MuiButton-root.Mui-disabled {
  pointer-events: all !important;
}

