.signup__formdiv {
  width: 20rem;
  border: 1px solid lightgray;
  margin: 50px auto 19px auto;
  text-align: center;
}

.signup__header_text {
  margin: 0px 40px 20px 40px;
}

.signup__formdiv1 {
  width: 20rem;
  padding: 20px 0;
  border: 1px solid lightgray;
  margin: 5px auto;
  text-align: center;
}

.signup__imagediv {
  margin: 20px 0;
}

.signup__inputdiv input {
  width: 70%;
  padding: 8px;
  margin: 3px;
}

.signup__inputdiv button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 77%;
  padding: 10px;
  margin: 20px auto;
  color: white;
  border: none;
  border-radius: 3px;
  background-color: #0095f6;
  cursor: pointer;
  border-radius: 2px;
}

.signup__inputdiv_button button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 77%;
  padding: 5px;
  margin: 20px auto;
  color: white;
  border: none;
  border-radius: 3px;
  background-color: #0095f6;
  cursor: pointer;
  border-radius: 2px;
}

button:disabled,
button[disabled] {
  opacity: 0.4;
  cursor: not-allowed;
}

.signup__hrline {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 13px;
}

.signup__hrline hr {
  width: 30%;
  margin: 20px 9px 20px 7px;
}

.signup__text {
  display: flex;
  justify-content: center;
  align-items: center;
}

.signup__text1 p {
  font-size: 11px;
  margin: 0px 30px 20px 30px;
  line-height: 0.9rem;
}

